import {MessageBox} from 'element-ui'

//JS操作cookies方法! 

//写cookies 

function setCookie(name,value,expirestime = 2) 
{ 
  var exp = new Date(); 
  exp.setTime(exp.getTime() + expirestime*60*60*1000); 
  document.cookie = name + '='+ escape (value) + ';expires=' + exp.toGMTString(); 
} 

//读取cookies 
function getCookie(name) 
{ 
  var arr,reg=new RegExp('(^| )'+name+'=([^;]*)(;|$)');
 
  if(arr=document.cookie.match(reg))
 
    return unescape(arr[2]); 
  else 
    return null; 
} 

//删除cookies 
function delCookie(name) 
{ 
  var exp = new Date(); 
  exp.setTime(exp.getTime() - 1); 
  var cval=getCookie(name); 
  if(cval!=null) 
    document.cookie= name + '='+cval+';expires='+exp.toGMTString(); 
}
// 删除的二次确认
function deleteAgain() {
  console.log('MessageBox', MessageBox)
  return MessageBox.confirm('此操作将永久删除该项, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
}

export {setCookie,getCookie,delCookie, deleteAgain};